import React, { useState, useEffect } from 'react';
import axios from 'axios';
import DogList from '../components/DogList';
import Nav from '../components/Nav';
import Footer from '../components/Footer';

const AdoptionPage = () => {
  const [dogs, setDogs] = useState([]);
  const [filteredDogs, setFilteredDogs] = useState([]);

  useEffect(() => {
    axios.get('http://localhost/capstone2/dog.php').then((response) => {
      setDogs(response.data);
      setFilteredDogs(response.data);
    });
  }, []);

  return (
    <div>
      <Nav/>

      {/* Banner */}
       <div className="container-fluid bg-overlay3">
          <div className="row text-center d-flex justify-content-center" style={{float: 'center'}}>
          <h1 className="pt-5 mt-5 bubbly-font" style={{fontSize: '60px'}}>Meet Our Dogs </h1>
            
           </div>
      </div><br/><br/>

      <div className='container'>
        <h5>We offer socially-distanced, outdoor shelter visits via appointment for potential adopters. We would love to help you find your new 4-legged friend! After viewing our pups, fill out an adoption or foster application below and we will contact you.</h5>
      </div> <br/><br/>

      <DogList dogs={filteredDogs} /><br/><br/><br/>

      <Footer/>
    </div>
  );
};

export default AdoptionPage;
