
import axios from 'axios';
import {useState, useEffect} from 'react';
import Dashboard from './Dashboard';

function App() {

  
  let [fname, setFname] = useState("");
  let [lname, setLname] = useState("");
  let [class_name, setClass_name] = useState("");
  const [students, setStudents] = useState([]);

  const url = 'http://localhost/capstone2/dogpage2.php';

  const handleFormSubmit = (e) => {
    e.preventDefault();
    // console.log('okk');
    let getData = new FormData ();
    getData.append('fname', fname);
    getData.append('lname', lname);
    getData.append('class', class_name);

    getData.append('function', 'insert');

    axios({
      url: url,
      method: 'POST', 
      data: getData,
      config: {header: {'Content-Type': 'multipart/form-data'}}
    }).then((response) => {
      alert('successful!');
      axios.get(url).then((response) => {
        setStudents(response.data);
      })
      setFname("");
      setLname("");
      setClass_name("");
    })
  }

  const delBtn = (e) => {
    let getData = new FormData();
    getData.append('function', 'delete');
    getData.append('id', e.currentTarget.id);

    // alert(e.currentTarget.id);
    axios({
      url: url,
      method: 'POST', 
      data: getData,
    }).then((response) => {
      alert('deleted!');
      axios.get(url).then((response) => {
        setStudents(response.data);
      })
    })

  }

  const updateBtn = (e) => {
    //e.preventDefault();
    //alert(e.currentTarget.id);
    let getData = new FormData();
    getData.append('upfname',document.getElementById('upfname' + e.currentTarget.id).value);
    getData.append('uplname',document.getElementById('uplname' + e.currentTarget.id).value);
    getData.append('upclass',document.getElementById('upclass' + e.currentTarget.id).value);
    getData.append('upid', e.currentTarget.id)
    // getData.append('upid',document.getElementById('upid').value);
    getData.append('function','update');
    axios({
      url: url,
      method: 'POST', 
      data: getData,
    }).then((response) => {
      alert('updated!');
      axios.get(url).then((response) => {
        setStudents(response.data);
      })
    })

  }

  //effect on initial load: so we implemented axios of fetching from this url and we through the data on setStudents which is an array. if we want to use it we just use this on the then
  useEffect(() => {
    axios.get(url).then((response) => {
      // console.log(response.data[0].fname);
      setStudents(response.data);
      // console.log(students);
    })
    
  },[]);

  return (
    <div>
      
      <Dashboard/>
      <div className="container-fluid">
      <h3>Dog Availability Record</h3><hr/>
      <form>
        Dog Name: <br />
        <input type="text" className="w-50 p-2 rounded-3 border-0" id="fname" name="fname" autocomplete="off" value={fname} onChange={ (e) => setFname(e.target.value) }></input><br/>
        Status: <br />
        <input type="text" className="w-50 p-2 rounded-3 border-0" id="lname" autocomplete="off" name="lname" value={lname} onChange={ (e) => setLname(e.target.value) } ></input><br/>
        Owner <br />
        <input type="text" className="w-50 p-2 rounded-3 border-0" id="class" name="class" autocomplete="off" value={class_name} onChange={ (e) => setClass_name(e.target.value) }></input><br/><br/>
        <button type='submit' className='btn blue-btn' onClick={handleFormSubmit}>Submit</button>
      </form>
      <hr/>

      <table className='table table-responsive table-bordered table-striped'>
        <thead>
          <th>Dog Name</th>
          <th>Status</th>
          <th>Owner</th>
          <th>Action/s</th>
          <th>Action/s</th>
        </thead>

        <tbody>
          {students.map((val) => {

            return(             

              <tr>
                {/* mapping. comes from data base columns. we also have stud_id to use for the button */}
                <td>{val.fname}</td>
                <td>{val.lname}</td>
                <td>{val.class}</td>
                <td><button id={val.stud_id} className="btn btn-outline-danger" onClick={delBtn}>Delete</button></td>
                
                <td>
                  <button type="button" id={val.stud_id} className="btn blue-btn" data-bs-toggle="modal" data-bs-target={"#exampleModal" + val.stud_id}>
                    Update Record
                  </button>

                  <div className="modal fade" id={"exampleModal"  + val.stud_id} tabindex="-1" aria-labelledby={"exampleModalLabel"  + val.stud_id} aria-hidden="true">
                    <div className="modal-dialog">
                      <div className="modal-content">
                        <div className="modal-header">
                          <h1 className="modal-title fs-5" id={"exampleModalLabel"  + val.stud_id}>Details</h1>
                          <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div className="modal-body">

                            <form>
                              <input type="hidden" id="upid" name="upid" defaultValue={val.stud_id}></input>
                              Dog Name: <br />
                              <input type="text"  className="input-search2 outline-0 p-2 w-75" id={"upfname" + val.stud_id} name="upfname" defaultValue={val.fname}></input><br/><br />
                              Status: <br />
                              <input type="text"  className="input-search2 outline-0 p-2 w-75" id={"uplname" + val.stud_id} name="uplname" defaultValue={val.lname}></input><br/><br />
                              Owner: <br />
                              <input type="text"   className="input-search2 outline-0 p-2 w-75" id={"upclass" + val.stud_id} name="upclass" defaultValue={val.class}></input><br/><br />
                              {/* <button type='submit' onClick={updateBtn}>Submit</button> */}

                              <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                <button type="submit" className="btn blue-btn" id={val.stud_id} onClick={updateBtn}>Save changes</button>
                              </div>
                                  
                            </form>

                        </div>
                       
                      </div>
                    </div>
                  </div>

                </td>

              </tr>
            )

          }
          )}
        
        </tbody>
      </table>

      </div>

    </div>
  );
}

export default App;