import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Nav from '../components/Nav';
import Footer from '../components/Footer';

function Contact(){

    const [formData, setFormData] = useState({
            name: '',
            email: '',
            subject: '',
            message: '',
    }); 
    const url = 'http://localhost/capstone2/contactform.php';

    const handleSubmit = (e) => {
            e.preventDefault();
          
            let getData = new FormData();
            getData.append('name', formData.name);
            getData.append('email', formData.email);
            getData.append('subject', formData.subject);
            getData.append('message', formData.message);

            getData.append('function', 'insert');
          
            axios({
              url: url,
              method: 'POST',
              data: getData,
            })
              .then((response) => {
                alert('Volunteer application submitted successfully!');
                setFormData({
                  name: '',
                  email: '',
                  subject: '',
                  message: '',
                });
              })
              .catch((error) => {
                console.error('Error submitting form:', error);
                alert('Error submitting form. Please try again.');
              });
    };


    useEffect(() => {
            const storedData = sessionStorage.getItem('formData');
            if (storedData) {
              setFormData(JSON.parse(storedData));
            }
     }, []);
    

    return(
        <div>
            <Nav/>

            <div className="container-fluid  p-5 page5">

                <section className="mb-4 contaier-fluid">
                    <h1 className="h1-responsive font-weight-bold text-center mb-4  display-6"><b>Contact us</b></h1>
                    <p className="text-center w-responsive mx-auto mb-5">Do you have any questions? Please do not hesitate to contact us directly. Our team will come back to you within
                        a matter of hours to help you.</p>

                    <div className="row">
                        <div className="col-md-9 mb-md-0 mb-5">
                    <form onSubmit={handleSubmit}>
                    <div className="row">
                                    <div className="col-md-6 my-3">
                                        <div className="md-form mb-0">
                                            <input type="text" id="name" name="name" className="form-control" required value={formData.name} onChange={(e) => setFormData({ ...formData, name: e.target.value })}  />
                                            <label for="name"  >Your name</label>
                                        </div>
                                    </div>
                            

                                    <div className="col-md-6 my-3">
                                        <div className="md-form mb-0">
                                            <input type="text" id="email" name="email" className="form-control" required value={formData.email} onChange={(e) => setFormData({ ...formData, email: e.target.value })}  />
                                            <label for="email" >Your email</label>
                                        </div>
                                    </div>

                                </div>

                                <div className="row my-3">
                                    <div className="col-md-12">
                                        <div className="md-form mb-0">
                                            <input type="text" id="subject" name="subject" className="form-control" required value={formData.subject} onChange={(e) => setFormData({ ...formData, subject: e.target.value })}  />
                                            <label for="subject" className="">Subject</label>
                                        </div>
                                    </div>
                                </div>

                                <div className="row my-3">

                                    <div className="col-md-12">

                                        <div className="md-form">
                                            <textarea type="text" id="message" name="message" rows="2" className="form-control md-textarea" required value={formData.message} onChange={(e) => setFormData({ ...formData, message: e.target.value })}  ></textarea>
                                            <label for="message">Your message</label>
                                        </div>

                                    </div>
                                </div>

                                <button type="submit" className="btn btn-warning">Submit</button>
                    </form>
                    </div>

                        <div className="col-md-3 text-center my-3">
                            <ul className="list-unstyled mb-0">
                                <li><i className="fas fa-map-marker-alt fa-2x"></i>
                                    <p>Philippines 12345</p>
                                </li>

                                <li><i className="fas fa-phone mt-4 fa-2x"></i>
                                    <p>+ 06 123 456 7899</p>
                                </li>

                                <li><i className="fas fa-envelope mt-4 fa-2x"></i>
                                    <p>contact@fuzzypaws.com</p>
                                </li>
                            </ul>
                        </div>

                    </div>

                </section>

            </div><br/><br/>


            <Footer/>

        </div>
    )
}

export default Contact;