import React from 'react';
import { Route, Switch, Routes } from 'react-router-dom';
import Dash2dogs from '../components/Dash2dogs';
import Dash2donors from '../components/Dash2donors';
import Sidebar from '../components/Dashside';

const Dashboard = () => {
  return (
    <div className="dashboard">
      <Sidebar />
      <div className="content">
      <Routes>
          <Route path="/Dash2dogs" element={<Dash2dogs />} />
          <Route path="/Dash2donors" element={<Dash2donors />} />
        </Routes>
      </div>
    </div>
  );
};

export default Dashboard;

