// export default DonateCard;


import React, { useState } from 'react';
import { useParams, Link } from 'react-router-dom';

function DonateCard({ cardData }) {
  const myImageStyle = { height: '180px' };

  return (
    <div>
        

        <div key={cardData.id} className="donate-card card d-inline-block m-2 border-0" style={{ width: '18rem' }}>
            <img src={cardData.donateImage} className="card-img-top m-0 p-0" alt="..." style={myImageStyle} />
        <div className="card-body h-100">
            <h5 className="card-title">{cardData.title}</h5>
            <p className="card-price">{cardData.price}</p>
            <p className="card-description">{cardData.desc}</p>
            {/* <Link to={`Donatedetail/${cardData.donatecard_id}`} className="btn btn-warning"> Details </Link> */}
            <Link to={`/Donatedetail/${cardData.id}`}  className="btn btn-warning">
               Details
            </Link>

        </div>
        </div>

    </div>
  );
}

export default DonateCard;