import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

const LoginPage = () => {
    const navigate = useNavigate();
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

  const handleLogin = async () => {
    try {
        
        console.log('Sending request:', { method: 'POST', headers: { 'Content-Type': 'application/json', }, body: JSON.stringify({ username: username, password: password, }), });

        const response = await fetch('http://localhost/capstone2/api2.php', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              username: username,
              password: password,
            }),
          });
      
          if (response.ok) {
            console.log('Login successful');
            alert('Login successful');
            navigate('/dashboard');
          } else {
            console.error('Login failed');
            const errorText = await response.text();
            console.error(errorText);
          }
        } catch (error) {
          console.error('Error during login:', error);
        }
      };

  return (
    <div className='container text-center'>
      <label>Username: <input type="text" className="w-75 p-2 rounded-3 border-0"  value={username} onChange={(e) => setUsername(e.target.value)} /></label><br/>
      <label>Password: <input type="password" className="w-75 p-2 rounded-3 border-0"  value={password} onChange={(e) => setPassword(e.target.value)} /></label> <br/><br/>
      <button className='btn btn-dark w-35' onClick={handleLogin}>Login</button>
    </div>
  );

  
};

export default LoginPage;

