import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { cardsData } from '../components/apidonate'; 
import Nav from '../components/Nav';
import axios from 'axios';
import Footer from '../components/Footer';


function Donatedetail() {
  const { id } = useParams();
  const navigate = useNavigate();
  const selectedCard = cardsData.find((card) => card.id === parseInt(id));

  const url = 'http://localhost/capstone2/donateform.php';

  const [formData, setFormData] = useState({
    name: '',
    email: '',
    number: '',
    address: '',
    province: '',
    city: '',
    zip: '',

  });

  // const [formData, setFormData] = useState([]);

  const [formStep, setFormStep] = useState(1);

  const handleInputChange = (e) => {
      setFormData((prevData) => ({
        ...prevData,
        [e.target.name]: e.target.value,
      }));
  };

  const handleNext = () => {
    setFormStep((prevStep) => prevStep + 1);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
      submitForm();
  };

  const submitForm = () => {
    let getData = new FormData();
    getData.append('name', formData.name);
    getData.append('email', formData.email);
    getData.append('number', formData.number);
    getData.append('address', formData.address);
    getData.append('province', formData.province);
    getData.append('city', formData.city);
    getData.append('zip', formData.zip);

    getData.append('function', 'insert');
    getData.append('memberTitle', selectedCard.title);
    getData.append('memberPrice', selectedCard.price);

    sessionStorage.removeItem('formData');

    setFormStep(2);

    axios({
        url: url,
        method: 'POST',
        data: getData,
        headers: { 'Content-Type': 'multipart/form-data' },
    })
    .then((response) => {
        const data = response.data;
        if (data.status === 'success') {
            alert(data.message);
            navigate('/home');
            setFormData({
                name: '',
                email: '',
                number: '',
                address: '',
                province: '',
                city: '',
                zip: '',
            });

            // setFormData((prevData) => [...prevData, data]);
            
        } else {
            console.error('Error submitting form:', data.message);
            alert('Error submitting form. Please try again.');
        }
    })
    .catch((error) => {
        console.error('Error submitting form:', error);
        alert('Error submitting form. Please try again.');
    });
  };

  const handlePrevious = () => {
    setFormStep(1);
  };

  // const updateBtn = (e) => {
  //   //e.preventDefault();
  //   //alert(e.currentTarget.id);
  //   let getData = new FormData();
  //   getData.append('upname',document.getElementById('upname' + e.currentTarget.id).value);
  //   getData.append('upemail',document.getElementById('upemail' + e.currentTarget.id).value);
  //   getData.append('upaddress',document.getElementById('upaddress' + e.currentTarget.id).value);
  //   getData.append('upid', e.currentTarget.id)
  //   // getData.append('upid',document.getElementById('upid').value);
  //   getData.append('function','update');
  //   axios({
  //     url: url,
  //     method: 'POST', 
  //     data: getData,
  //   }).then((response) => {
  //     alert('updated!');
  //     axios.get(url).then((response) => {
  //       setFormData(response.data);
  //     })
  //   })

  // }

  
  useEffect(() => {
    
    const storedData = sessionStorage.getItem('formData');
    if (storedData) {
      setFormData(JSON.parse(storedData));
    }
  }, []);

  return (
    <div>

      <Nav/>

      <div className='text-center'>
        <img src="https://cdn-icons-png.flaticon.com/128/2960/2960221.png" style={{width: '90px', height: '90px'}}/>
        </div>
      <h2 className='text-center my-3'>Thank you for your Support!</h2><hr/><br/>

      {/* FORMS */}
      <div className='container'>

      <div className='row'>

        <div className='col-md-6 dodet-info rounded-3'>
          <p className='hidden'>Card ID: {id}</p>

          <img className='rounded-3 my-4' src={selectedCard.donateImage} alt={selectedCard.title} style={{  maxWidth: '100%' }} />
          <h3>{selectedCard.title}</h3>
          <p>Price: {selectedCard.price}</p>
          <p>Description: {selectedCard.desc}</p>
          <p>{selectedCard.desc2}</p>
        </div>

        <div className='col-md-6 dodet-card px-5 mt-1'>
        {formStep === 1 && (
        <form onSubmit={handleSubmit}>
          {/* Customer Details */}
            <div>
              <h5><b>Customer Details</b></h5><br/>
            <label htmlFor="name">Complete Name: *</label> <br/>
              <input className="w-75 p-2 rounded-3 border-0" type="text" id="name" name="name" value={formData.name} onChange={handleInputChange} required />
            </div> <br/>

            <div>
              <label htmlFor="email">Email: *</label> <br/>
              <input className="w-75 p-2 rounded-3 border-0" type="email" id="email" name="email" value={formData.email} onChange={handleInputChange} autoComplete="off" required/>
            </div>  <br/>

            <div>
              <label htmlFor="number">Mobile Number:</label>  <br/>
              <input className="w-75 p-2 rounded-3 border-0" type="number" id="number" name="number" value={formData.number} onChange={handleInputChange} autoComplete="off" />
            </div>  <br/>

          {/* Billing Address */}
            <div>
            <h5><b>Billing Address</b></h5><br/>
              <label htmlFor="address">Street Address / Building Name: *</label>  <br/>
              <input className="w-75 p-2 rounded-3 border-0" type="text" id="address" name="address" value={formData.address} onChange={handleInputChange} autoComplete="off" required />
            </div>  <br/>

            <div>
              <label htmlFor="province">Province:</label>  <br/>
              <input className="w-75 p-2 rounded-3 border-0" type="text" id="province" name="province" value={formData.province} onChange={handleInputChange} autoComplete="off" />
            </div>  <br/>

            <div>
              <label htmlFor="city">City: *</label>  <br/>
              <input className="w-75 p-2 rounded-3 border-0" type="text" id="city" name="city" value={formData.city} onChange={handleInputChange} autoComplete="off" required/>
            </div>  <br/>

            <div>
              <label htmlFor="zip">Zip Code: *</label>  <br/>
              <input className="w-75 p-2 rounded-3 border-0" type="number" id="zip" name="zip" value={formData.zip} onChange={handleInputChange} autoComplete="off" required />
            </div>  <br/>

            <button className='btn orange-btn mx-3 my-3' type="button" onClick={handleNext}>
              Next
            </button>


          {/* <button type="submit">Proceed to Payment</button> */}
        </form>
        )}

        
        {formStep === 2 && (
          <form onSubmit={handleSubmit}>

          <h5><b>Payment Address</b></h5><br/>
          
          <div className='d-inline-block'>
            <img className='me-2' src="https://cdn-icons-png.flaticon.com/128/349/349221.png" style={{width: '40px', height: '40px'}}/>
            <img className='me-2' src="https://cdn-icons-png.flaticon.com/128/11041/11041055.png" style={{width: '40px', height: '30px'}}/>
            <img className='me-2' src="https://cdn-icons-png.flaticon.com/128/196/196565.png" style={{width: '40px', height: '40px'}}/>
            <img className='me-2' src="https://cdn-icons-png.flaticon.com/128/5977/5977576.png" style={{width: '40px', height: '40px'}}/>
          </div> <br/><br/>

          <label htmlFor="fullName">Full Name: *</label> <br/>
              <input className="w-75 p-2 rounded-3 border-0" type="text" id="fullName" name="fullName" placeholder="Enter Card number" maxLength="16"  required /> <br/><br/>
            
            <label htmlFor="cardNumber">Card Number: *</label> <br/>
              <input className="w-75 p-2 rounded-3 border-0" type="text" id="cardNumber" name="cardNumber" placeholder="Enter Card number" maxLength="16" required /> <br/><br/>

              <label htmlFor="expiryDate">Expiration Date (MM/YY): *</label> <br/>
              <input className="w-75 p-2 rounded-3 border-0" type="text" id="expiryDate" name="expiryDate" placeholder="MM/YY" maxLength="5" pattern="\d\d/\d\d" required /> <br/><br/>

              <label htmlFor="cvv">CVV Code: *</label> <br/>
              <input className="w-75 p-2 rounded-3 border-0" type="text" id="cvv" name="cvv" placeholder="Enter CVV" maxLength="3" pattern="\d{3}" required /> <br/><br/>

            <button className='btn orange-btn mx-3 my-3' type="button" onClick={handlePrevious}>
              Previous
            </button>

            <button className='btn blue-btn mx-3 my-3' type="submit">Proceed to Payment</button>
          </form>
        )}
        </div>
        </div>

      </div>
      

      {/* TABLE */}
      {/* <table className='table table-bordered table-striped'>
        <thead>
          <th>First Name</th>
          <th>Last Name</th>
          <th>Class Name</th>
          <th>Action/s</th>
          <th>Action/s</th>
        </thead>

        <tbody>
          {formData.map((val) => {

            return(             

              <tr key={val.donateform_id}>
                <td>{val.email}</td>
                <td>{val.address}</td>
  
                
                <td>
                  <button type="button" id={val.donateform_id} className="btn btn-primary" data-bs-toggle="modal" data-bs-target={"#exampleModal" + val.donateform_id}>
                    Update Record
                  </button>

                  <div className="modal fade" id={"exampleModal"  + val.donateform_id} tabindex="-1" aria-labelledby={"exampleModalLabel"  + val.donateform_id} aria-hidden="true">
                    <div className="modal-dialog">
                      <div className="modal-content">
                        <div className="modal-header">
                          <h1 className="modal-title fs-5" id={"exampleModalLabel"  + val.donateform_id}>Modal title</h1>
                          <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div className="modal-body">

                            <form>
                              <input type="hidden" id="upid" name="upid" defaultValue={val.donateform_id}></input>
                              First Name: <br />
                              <input type="text" id={"upfname" + val.donateform_id} name="upfname" defaultValue={val.fname}></input><br/>
                              Last Name: <br />
                              <input type="text" id={"uplname" + val.donateform_id} name="uplname" defaultValue={val.lname}></input><br/>
                              Class: <br />
                              <input type="text" id={"upclass" + val.donateform_id} name="upclass" defaultValue={val.class}></input><br/>
                              <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                <button type="submit" className="btn btn-primary" id={val.donateform_id} onClick={updateBtn}>Save changes</button>
                              </div>
                                  
                            </form>

                        </div>
                       
                      </div>
                    </div>
                  </div>

                </td>

              </tr>
            )

          }
          )}
        
        </tbody>
      </table> */}


      <br/><br/><br/><br/>
      <Footer/>

    </div>
  );
}

export default Donatedetail;
