import Carousel from 'react-bootstrap/Carousel';
// import one from '../img/1.jpg';
// import two from '../img/2.png';

function CarouselFadeExample() {

  const gradientStyle = {
    background: 'linear-gradient( rgba(151, 193, 255, ), #f1807c)',
    maxHeight: '65vh',
  };

  const overlayStyle = {
    content: "''",
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    background: 'rgba(5, 0, 0, 0.3)',
    zIndex: 1, 
  };

  return (
    <Carousel fade>
      <Carousel.Item>
        <img src="https://static01.nyt.com/images/2013/10/06/sunday-review/06DOGS/06DOGS-superJumbo.jpg " className="d-block w-100"  alt="First slide" style={{ maxHeight: '69vh', objectFit: 'cover' }} text="First slide"/>
        <div style={overlayStyle}></div>
        <Carousel.Caption>
          <h1 className='bubbly-font bubbly-size'>Our Mission</h1>
          <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img src="https://api.time.com/wp-content/uploads/2015/04/dog-child.jpg" className="d-block w-100" style={{ maxHeight: '69vh', objectFit: 'cover' }}  text="Second slide" />
        <div style={overlayStyle}></div>
        <Carousel.Caption>
          <h1 className='bubbly-font bubbly-size'>Our History</h1>
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img src="https://cdn-prd.content.metamorphosis.com/wp-content/uploads/sites/2/2020/09/shutterstock_754292557-2.jpg" className="d-block w-100" text="Third slide" style={{ maxHeight: '69vh', objectFit: 'cover' }} />
        <div style={overlayStyle}></div>
        <Carousel.Caption>
          <h1  className='bubbly-font bubbly-size'>Our Vision</h1>
          <p>
            Praesent commodo cursus magna, vel scelerisque nisl consectetur.
          </p>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
  );
}

export default CarouselFadeExample;