import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Dashboard from './Dashboard';

function DonatorPage2() {
  const [donationForms, setDonationForms] = useState([]);

  useEffect(() => {
    fetch('http://localhost/capstone2/donatorpage2.php') 
        .then(response => response.json())
        .then(data => setDonationForms(data))
        .catch(error => console.error('Error fetching dog list:', error));
}, []);

return (
    <div>

        <Dashboard/>
      <h3>Donation Forms</h3>
      <table className="table table-bordered table-striped">
        <thead>
          <tr>
            <th>Name</th>
            <th>Member Title</th>
            <th>Member Price</th>
          </tr>
        </thead>
        <tbody>
          {console.log(donationForms)}
          {donationForms.map(form => (
            <tr key={form.id}>
              <td>{form.name}</td>
              <td>{form.memberTitle}</td>
              <td>{form.memberPrice}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default DonatorPage2;
