import React, { useState } from 'react';
import DonateCard from './DonateCard';
import { Link } from 'react-router-dom';
import Nav from '../components/Nav';
import { cardsData } from './apidonate';

function DonateAll() {
  
    return (
      <div className="mt-5 container donate-container d-flex flex-wrap justify-content-center">
        {cardsData.map((card) => (
          <DonateCard key={card.id} cardData={card} />
          // <Link key={card.id} to={{ pathname: `/Donatedetail/${card.id}`, state: { cardData: card } }}>
          // <DonateCard cardData={card} />
          // </Link>
        ))}
      </div>
    );
  }
  
  export default DonateAll;