// with table
import React, { useState, useRef, useEffect, useContext } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import axios from 'axios';

function Register() {
  const navigate = useNavigate();

  const [fullname, setFullname] = useState("");
  const [email, setEmail] = useState("");
  const [contactNumber, setContactNumber] = useState("");
  const [zipcode, setZipcode] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [fullnameError, setFullnameError] = useState("");
  const [emailError, setEmailError] = useState("");
  const [contactNumberError, setContactNumberError] = useState("");
  const [zipcodeError, setZipcodeError] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [confirmPasswordError, setConfirmPasswordError] = useState("");
  const inputReference = useRef();

  const [students, setStudents] = useState([]);

  const url = 'http://localhost/capstone2/register.php';

  const fullnameVerify = () => {
    if (fullname.length >= 2) {
      setFullnameError("");
      return true;
    }
    setFullnameError("Full name must be at least 2 characters");
    return false;
  };

  const emailVerify = () => {
    const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (emailPattern.test(email)) {
      setEmailError("");
      return true;
    }
    setEmailError("Invalid email format");
    return false;
  };

  const contactNumberVerify = () => {
    setContactNumberError("");
    return true;
  };

  const zipcodeVerify = () => {
    setZipcodeError("");
    return true;
  };

  const passwordVerify = () => {
    if (password.length >= 10) {
      setPasswordError("");
      return true;
    }
    setPasswordError("Password must be at least 10 characters");
    return false;
  };

  const confirmPasswordVerify = () => {
    if (confirmPassword === password) {
      setConfirmPasswordError("");
      return true;
    }
    setConfirmPasswordError("Passwords do not match");
    return false;
  };

  const validated = () => {
    const isFullnameValid = fullnameVerify();
    const isEmailValid = emailVerify();
    const isContactNumberValid = contactNumberVerify();
    const isZipcodeValid = zipcodeVerify();
    const isPasswordValid = passwordVerify();
    const isConfirmPasswordValid = confirmPasswordVerify();

    return (
      isFullnameValid &&
      isEmailValid &&
      isContactNumberValid &&
      isZipcodeValid &&
      isPasswordValid &&
      isConfirmPasswordValid
    );
  };

  const submitForm = (e) => {
    e.preventDefault();

    if (validated()) {
      let code = Math.random();
      sessionStorage.setItem("token", code);
    
      let getData = new FormData();

      getData.append('fullname', fullname);
      getData.append('email', email);
      getData.append('contactNumber', contactNumber);
      getData.append('zipcode', zipcode);
      getData.append('password', password);
 
      getData.append('function', 'insert');

      axios({
        url: url,
        method: 'POST',
        data: getData,
        config: { headers: { 'Content-Type': 'multipart/form-data' } },
      })
        .then((response) => {
          alert('Registration successful!');
          axios.get(url).then((response) => {
            setStudents(response.data);
          })
          setFullname("");
          setEmail("");
          setContactNumber("");
          setZipcode("");
          setPassword("");
          navigate('/login');
        })
        .catch((error) => {
          console.error("Registration failed:", error);
        });
    } else {
      alert("Please enter valid information");
    }
  };


  const delBtn = (e) => {
    let getData = new FormData();
    getData.append('function', 'delete');
    getData.append('id', e.currentTarget.id);

    // alert(e.currentTarget.id);
    axios({
      url: url,
      method: 'POST', 
      data: getData,
    }).then((response) => {
      alert('deleted!');
      axios.get(url).then((response) => {
        setStudents(response.data);
      })
    })

  }

  const updateBtn = (e) => {
    //e.preventDefault();
    //alert(e.currentTarget.id);
    let getData = new FormData();
    getData.append('upfullname',document.getElementById('upfullname' + e.currentTarget.id).value);
    getData.append('upemail',document.getElementById('upemail' + e.currentTarget.id).value);
    getData.append('upcontactNumber',document.getElementById('upcontactNumber' + e.currentTarget.id).value);
    getData.append('upemail',document.getElementById('upemail' + e.currentTarget.id).value);
    getData.append('upzipcode',document.getElementById('upzipcode' + e.currentTarget.id).value);
    getData.append('uppassword',document.getElementById('uppassword' + e.currentTarget.id).value);
    getData.append('upid', e.currentTarget.id)

    getData.append('function','update');
    axios({
      url: url,
      method: 'POST', 
      data: getData,
    }).then((response) => {
      alert('updated!');
      axios.get(url).then((response) => {
        setStudents(response.data);
      })
    })

  }

  useEffect(() => {
    axios.get(url).then((response) => {
      // console.log(response.data[0].fname);
      setStudents(response.data);
      // console.log(students);
    })
  }, []);


  return (
    <div>

      <form className="text-center p-5 wrapper">
        <h1 className="display-6"><b>REGISTER</b></h1>

        <label className="my-3 fs-5">Full Name: </label> <br />
        <input ref={inputReference} className="rounded-3 p-2 " type="text" id="fullname" autoComplete="off" value={fullname} onChange={(e) => { setFullname(e.target.value); }} onBlur={fullnameVerify} /> <br /><br />

        {fullnameError && <p style={{ color: 'red' }}>{fullnameError}</p>}

        <label className="my-3 fs-5">Email: </label> <br />
        <input className="rounded-3 p-2 " type="email" id="email" autoComplete="off" value={email} onChange={(e) => { setEmail(e.target.value); }} onBlur={emailVerify} /> <br /><br />

        {emailError && <p style={{ color: 'red' }}>{emailError}</p>}

        <label className="my-3 fs-5">Contact Number: </label> <br />
        <input className="rounded-3 p-2 " type="number" id="contactNumber" autoComplete="off" value={contactNumber} onChange={(e) => { setContactNumber(e.target.value); }} onBlur={contactNumberVerify} /> <br /><br />

        {contactNumberError && <p style={{ color: 'red' }}>{contactNumberError}</p>}

        <label className="my-3 fs-5">Zipcode: </label> <br />
        <input className="rounded-3 p-2 " type="number" id="zipcode" autoComplete="off" value={zipcode} onChange={(e) => { setZipcode(e.target.value); }} onBlur={zipcodeVerify} /> <br /><br />

        {zipcodeError && <p style={{ color: 'red' }}>{zipcodeError}</p>}

        <label className="my-3 fs-5">Password: </label> <br />
        <input className="rounded-3 p-2" type="password" id="password" autoComplete="off" value={password} onChange={(e) => { setPassword(e.target.value); }} onBlur={passwordVerify} /> <br /> <br />

        {passwordError && <p style={{ color: 'red' }}>{passwordError}</p>}

        <label className="my-3 fs-5">Confirm Password: </label> <br />
        <input className="rounded-3 p-2" type="password" id="confirmPassword" autoComplete="off" value={confirmPassword} onChange={(e) => { setConfirmPassword(e.target.value); }} onBlur={confirmPasswordVerify} /> <br /> <br />

        {confirmPasswordError && <p style={{ color: 'red' }}>{confirmPasswordError}</p>}

        <button className="my-btn btn text-light my-3 fs-5" type="submit" onClick={submitForm}>Register</button> <br />

        <Link id="nav-title" className="nav-title navbar-brand fs-5" to="/login/user">
          <button className="my-btn btn text-light my-3 fs-5" type="button">Login</button>
        </Link>

      </form>
      <hr/>

      <table className='table table-bordered table-striped'>
        <thead>
          <th>Full Name</th>
          <th>Email</th>
          <th>Contact Number</th>
          <th>Zipcode</th>
          <th>Password</th>
        </thead>

        <tbody>
          {students.map((val) => {

            return(             

              <tr>
                <td>{val.fullname}</td>
                <td>{val.email}</td>
                <td>{val.contactNumber}</td>
                <td>{val.zipcode}</td>
                <td>{val.password}</td>
                <td><button id={val.register_id} className="btn btn-outline-danger" onClick={delBtn}>Delete</button></td>
                
                <td>
                  <button type="button" id={val.register_id} className="btn btn-primary" data-bs-toggle="modal" data-bs-target={"#exampleModal" + val.register_id}>
                    Update Record
                  </button>

                  <div className="modal fade" id={"exampleModal"  + val.register_id} tabindex="-1" aria-labelledby={"exampleModalLabel"  + val.register_id} aria-hidden="true">
                    <div className="modal-dialog">
                      <div className="modal-content">
                        <div className="modal-header">
                          <h1 className="modal-title fs-5" id={"exampleModalLabel"  + val.register_id}>Modal title</h1>
                          <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div className="modal-body">

                            <form>
                              <input type="hidden" id="upid" name="upid" defaultValue={val.register_id}></input>
                              Full Name: <br />
                              <input type="text" id={"upfullname" + val.register_id} name="upfullname" defaultValue={val.fullname}></input><br/>
                              Emaile: <br />
                              <input type="text" id={"upemail" + val.register_id} name="upemail" defaultValue={val.email}></input><br/>
                              Contact Number: <br />
                              <input type="text" id={"upcontactNumber" + val.register_id} name="upcontactNumber" defaultValue={val.contactNumber}></input><br/>
                              Zipcode: <br />
                              <input type="text" id={"upzipcode" + val.register_id} name="upzipcode" defaultValue={val.zipcode}></input><br/>
                              Password: <br />
                              <input type="text" id={"uppassword" + val.register_id} name="uppassword" defaultValue={val.password}></input><br/>

                              <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                <button type="submit" className="btn btn-primary" id={val.register_id} onClick={updateBtn}>Save changes</button>
                              </div>
                                  
                            </form>

                        </div>
                       
                      </div>
                    </div>
                  </div>

                </td>

              </tr>
            )

          }
          )}
        
        </tbody>
      </table>



    </div>
  );
}

export default Register;



