import React, { useState, useEffect } from 'react';

const UserPage = () => {
  const [userList, setUserList] = useState([]);
  const [newUser, setNewUser] = useState({ name: '', email: '' });

  useEffect(() => {
    // Fetch the list of users from the backend
    // Use the fetch or Axios library to make a GET request to your PHP backend
    // Update the userList state with the retrieved data
    fetch('/api/users')  // Replace with your actual API endpoint
      .then(response => response.json())
      .then(data => setUserList(data))
      .catch(error => console.error('Error fetching users:', error));
  }, []);

  const handleAddUser = () => {
    // Send a POST request to add a new user to the backend
    fetch('/api/users', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(newUser),
    })
      .then(response => response.json())
      .then(data => {
        // Update the userList state with the new user
        setUserList(prevUsers => [...prevUsers, data]);
        // Clear the input fields
        setNewUser({ name: '', email: '' });
      })
      .catch(error => console.error('Error adding user:', error));
  };

  const handleDeleteUser = (userId) => {
    // Send a DELETE request to remove a user from the backend
    fetch(`/api/users/${userId}`, {
      method: 'DELETE',
    })
      .then(() => {
        // Update the userList state by removing the deleted user
        setUserList(prevUsers => prevUsers.filter(user => user.id !== userId));
      })
      .catch(error => console.error('Error deleting user:', error));
  };

  return (
    <div>
      <h2>Manage Users</h2>
      <ul>
        {userList.map((user) => (
          <li key={user.id}>
            {user.name} - {user.email}
            <button onClick={() => handleDeleteUser(user.id)}>Delete</button>
          </li>
        ))}
      </ul>

      <h3>Add New User</h3>
      <label>Name: <input type="text" value={newUser.name} onChange={(e) => setNewUser({ ...newUser, name: e.target.value })} /></label>
      <label>Email: <input type="text" value={newUser.email} onChange={(e) => setNewUser({ ...newUser, email: e.target.value })} /></label>
      <button onClick={handleAddUser}>Add User</button>
    </div>
  );
};

export default UserPage;
