// export default Search;


import React, { useState } from 'react';


const Search = ({ onSearch }) => {
    const [searchTerm, setSearchTerm] = useState('');
    const [ageFilter, setAgeFilter] = useState('');
    const [sexFilter, setSexFilter] = useState('');

  const handleSearch = () => {
    const filters = {
      name: searchTerm,
      age: ageFilter,
      sex: sexFilter,
    };

    onSearch(filters);
  };
  
    const clearSearch = () => {
    const filters = {
        name: '',
        age: '',
        sex: '',
    };

    setSearchTerm('');
    setAgeFilter('');
    setSexFilter('');
    onSearch(filters);
    };


  return (
    <div className='container align-items-center'>
      <div className='row search-adopt rounded-4'>
        <div className="col-md-8">
      <label htmlFor="searchType">Search:</label><br/>
      <input type="text" id="searchType" className="input-search2 outline-0 p-2 w-75" value={searchTerm} onChange={(e) => setSearchTerm(e.target.value)}/>  <br/>

      <select id="ageFilter" className="p-2 rounded-3 w-75 my-3 input-search" value={ageFilter} onChange={(e) => setAgeFilter(e.target.value)}>
        <option value="">Select Age</option>
        <option value="baby">Baby</option>
        <option value="young">Young</option>
        <option value="adult">Adult</option>
        <option value="senior">Senior</option>
      </select> <br/> 

      <select id="sexFilter" className="p-2 rounded-3 w-75 my-3 input-search" value={sexFilter} onChange={(e) => setSexFilter(e.target.value)}>
        <option value="">Select Sex</option>
        <option value="girl">Girl</option>
        <option value="boy">Boy</option>
      </select> <br/> 

      <button className="btn search-btn w-75 my-3" onClick={handleSearch}>Search</button><br/>
      <button className="btn search-btn w-75" onClick={clearSearch}>Clear Search</button> <br/>
      </div>

      <div className="col-md-4 text-center p-4">
      <img className="mt-4" src="https://cdn-icons-png.flaticon.com/256/7592/7592531.png" style={{width: '250px', height: '290px'}}/>
      </div>

      {/* <img src="https://cdn-icons-gif.flaticon.com/12893/12893807.gif"></img> */}
      </div><br/><br/><br/><br/>
    </div>
  );
};

export default Search;
