import React from 'react';
import { Link, Route, Routes } from 'react-router-dom';
import Dogpage2 from './Dogpage2';
import Donatorpage2 from './Donatorpage2';



const Dashboard = () => {
  return (
    <div className='py-5 mx-3'>
      
      <Link id="nav-title" className="nav-title navbar-brand" to="/home">
      <b style={{fontSize: '35px'}}>Fuzzy Paws</b>
      </Link><br/><br/>
      <h2>Admin Dashboard</h2><br/>

      <nav className='sidebar'>
      
          <li>
            <img className='me-2' src='https://cdn-icons-png.flaticon.com/128/6088/6088559.png' style={{width: '25px', height: '25px'}}/>
            <Link to="/Dogpage2" className="text-dark me-3" style={{textDecoration: 'none'}}>Manage Dogs</Link>
          </li>
          <li>
            <img className='me-2' src='https://cdn-icons-png.flaticon.com/128/64/64380.png' style={{width: '30px', height: '30px'}}/>
            <Link to="/Donatorpage2"  className="text-dark me-3" style={{textDecoration: 'none'}}> Donators List</Link>
          </li>
        
      </nav>

      <Routes>
        {/* <Route path="dogs" element={<Dogpage />} /> */}
        <Route path="users" element={<Donatorpage2 />} />
      </Routes>

    </div>
  );
};

export default Dashboard;





