import React from 'react';
import { useState } from 'react';
import { Link, Route, Routes } from 'react-router-dom';
import Nav from '../components/Nav';
import Loginuser from './Loginuser';
import Loginadmin from './Loginadmin';

function LoginPage() {

  const url = 'http://localhost/capstone2/api.php';

  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  
  const handleLogin = () => {
  }

  return (
    <div>
    

    {/* Centered boxes with styling */}
    <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '80vh' }}>
      <div className='text-light login-admin' style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', padding: '20px', border: '1px solid #ccc', borderRadius: '8px', margin: '10px' }}>
        <h3>Guest</h3>
        <img src="https://cdn-icons-png.flaticon.com/128/4253/4253264.png" className='img-fluid'/>
        <Link to="/home"  className="text-light" style={{ textDecoration: 'none' }}>Proceed as a Guest</Link>
      </div>

      <div className=' text-light login-admin' style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', padding: '20px', border: '1px solid #ccc', borderRadius: '8px', margin: '10px' }}>
        <h3>Admin</h3>
        <img src="https://cdn-icons-png.flaticon.com/128/1962/1962536.png" className='img-fluid'/>
        <Link to="/login/admin"  className="text-light" style={{ textDecoration: 'none' }}>Login as an Admin</Link>
      </div>
      

    </div>

    <Routes>
      {/* <Route path="user" element={<Loginuser />} /> */}
      <Route path="admin" element={<Loginadmin />} />
    </Routes>
  </div>
  );
}

export default LoginPage;
