import { useState, useRef, useContext, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import UserContext from './data';
import axios from 'axios';
import { Link } from 'react-router-dom';

function Login() {

  let { setUser } = useContext(UserContext);
  let [email, setEmail] = useState("");
  let [password, setPassword] = useState("");
  let navigate = useNavigate();
  let inputReference = useRef();
  const [emailError, setEmailError] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [students, setStudents] = useState([]);
  const url = 'http://localhost/capstone2/api.php';

  const emailVerify = () => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;

    if (emailRegex.test(email)) {
      setEmailError("");
      return true;
    }
    setEmailError("Please enter a valid email address");

    return false;
  };

  const passwordVerify = () => {
    if (password.length >= 10) {
      setPasswordError("");
      return true;
    }
    setPasswordError("Password must be at least 10 characters");
    return false;
  };

  const validated = () => {
    const isEmailValid = emailVerify();
    const isPasswordValid = passwordVerify();
    return isEmailValid && isPasswordValid;
  };

  const submitForm = (e) => {
    e.preventDefault();
    if (validated()) {
      let getData = new FormData();
      getData.append('email', email);
      getData.append('password', password);
      getData.append('function', 'login');
  
      axios({
        url: url,
        method: 'POST',
        data: getData,
        headers: { 'Content-Type': 'multipart/form-data' }
      })
      .then((response) => {
        const result = response.data;
      
        if (result.status === 'success') {
          alert('Login successful!');
          let fullname = result.username;
          let code = Math.random();
          sessionStorage.setItem('token', code);
          setUser(fullname);
          navigate('/home');
        } else {
          alert('Login failed. Please check your credentials.');
        }
      })
      
        
    } else {
      alert('Please enter a valid email and password');
    }
  };
  
    const handleLogin = () => {
      // Add logic for authenticating the admin (we'll implement this later)
    }

  useEffect(() => {
    axios.get(url).then((response) => {
      setStudents(response.data);
    });
  }, []);

  return (
    <div>
      
      <form className="text-center p-5 wrapper">
        <h1 className="display-6"><b>LOGIN</b></h1>
        <label className="my-3 fs-5">Email: </label> <br />
        <input ref={inputReference} className="rounded-3 p-2 " type="email" id="email" autoComplete="off" value={email} onChange={(e) => { setEmail(e.target.value); }} onBlur={emailVerify} /> <br /><br />

        {emailError && <p style={{ color: 'red' }}>{emailError}</p>}

        <label className="my-3 fs-5">Password: </label> <br />
        <input className="rounded-3 p-2" type="password" id="password" autoComplete="off"value={password}  onChange={(e) => { setPassword(e.target.value); }} onBlur={passwordVerify} /> <br /> <br />

        {passwordError && <p style={{ color: 'red' }}>{passwordError}</p>}

        <button className="my-btn btn text-light my-3 fs-5" type="button" onClick={submitForm}>Login</button> <br />

        <Link id="nav-title" className="nav-title navbar-brand fs-5" to="/register">
          <button className="my-btn btn text-light my-3 fs-5" type="button" >Register</button>
        </Link>
        
      </form>
    
    </div>
  );
}

export default Login;