// ORGINAL
import React, { useState, useRef, useEffect, useContext } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import axios from 'axios';
import Nav from '../components/Nav';


function Volunteer () {

  const pdfFile = () => {
    const pdfUrl = "Sample.pdf";
    const link = document.createElement("a");
    link.href = pdfUrl;
    link.download = ""; 
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
};


    const [formData, setFormData] = useState({
        name: '',
        email: '',
        image: '',
      });

        const [volunteerz, setVolunteerz] = useState([]);
        const [img, setImg] = useState('');

        const url = 'http://localhost/capstone2/volunteerform.php';

        const getImg = (e) => {
            let input_file = document.getElementById('choosefile');
            let x = input_file.files[0];
        
            let reader = new FileReader();
            reader.addEventListener('load', function () {
              sessionStorage.setItem('image', reader.result);
              let src = sessionStorage.getItem('image');
              let img = document.getElementById('pic');
              img.setAttribute('src', src);
            });
            reader.readAsDataURL(x);
        };

// const handleFormSubmit = (e) => {
//   e.preventDefault();

//   let getData = new FormData();
//   getData.append('name', formData.name);
//   getData.append('email', formData.email);
//   getData.append('choosefile', formData.image); // Use 'choosefile' as the key
//   getData.append('function', 'insert');

//   axios({
//     url: url,
//     method: 'POST',
//     data: getData,
//     config: { header: { 'Content-Type': 'multipart/form-data' } },
//   })
//     .then((response) => {
//       alert('Volunteer application submitted successfully!');
//       setFormData({
//         name: '',
//         email: '',
//         image: '',
//       });
//       document.getElementById('choosefile').value = '';
//     })
//     .catch((error) => {
//       console.error('Error submitting form:', error);
//       alert('Error submitting form. Please try again.');
//     });
// };

const handleFormSubmit = (e) => {
    e.preventDefault();
  
    let getData = new FormData();
    getData.append('name', formData.name);
    getData.append('email', formData.email);
    getData.append('image', e.target.choosefile.files[0]); // Use e.target.choosefile.files[0] as the file data
    getData.append('function', 'insert');
  
    axios({
      url: url,
      method: 'POST',
      data: getData,
      // Remove config, as it's not necessary for Axios
    })
      .then((response) => {
        alert('Volunteer application submitted successfully!');
        setFormData({
          name: '',
          email: '',
          image: '',
        });
        document.getElementById('choosefile').value = '';
      })
      .catch((error) => {
        console.error('Error submitting form:', error);
        alert('Error submitting form. Please try again.');
      });
  };
  

    useEffect(() => {
    
        const storedData = sessionStorage.getItem('formData');
        if (storedData) {
          setFormData(JSON.parse(storedData));
        }
      }, []);


    return(
        <div>

            <Nav/>

    <h1>Volunteer Application Forms</h1>
    <p> Download PDF file for Application. Please fill it in and upload it down below!</p>
    <button onClick={pdfFile}> Download PDF </button>


      <form onSubmit={handleFormSubmit}>
        {/* Name input */}
        <label htmlFor="name">Name:</label>
        <input type="text" id="name" name="name" value={formData.name} onChange={(e) => setFormData({ ...formData, name: e.target.value })} required />


        {/* Email input */}
        <label htmlFor="email">Email:</label>
       <input type="email" id="email" name="email" value={formData.email} onChange={(e) => setFormData({ ...formData, email: e.target.value })} required />


        {/* Image upload input */}
        <label htmlFor="choosefile">Choose File:</label>
            <input type="file" name="choosefile" id="choosefile" onChange={getImg} />
            <img id="pic" style={{ width: '100px', height: '100px' }} />
            <br />

        {/* Submit button */}
        <button type="submit">Submit</button>
      </form>
      
    </div>
  );
};

export default Volunteer;




