import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import Nav from '../components/Nav';
import Footer from '../components/Footer';

const AdoptDetail = () => {
  const { id } = useParams();
  const [dogDetails, setDogDetails] = useState({});

  useEffect(() => {
    fetch(`http://localhost/capstone2/dog2.php?id=${id}`)
      .then((response) => response.json())
      .then((data) => setDogDetails(data))
      .catch((error) => console.error('Error fetching dog details:', error));
  }, [id]);

  const sliderSettings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 768,
        settings: {
          arrows: false,
        },
      },
    ],
  };

  return (
    <div>
      <Nav />

      <div className='container full-detail p-3'>
        <div className="container adoptdetail-cont" style={{ maxWidth: '800px', margin: '0 auto' }}>
            {/* Adjust the maxWidth and margin values as needed */}
            <div className="row">
            <h1 className="bubbly-font bubbly-size text-center">{dogDetails.name} Details</h1>
            <div className="col-md-6 mt-3">
                <Slider {...sliderSettings} className="container">
                <img src={dogDetails.fileimg} alt={dogDetails.name} className="img-fluid" style={{ maxWidth: '100%' }} />
                <img src={dogDetails.dimage} alt={dogDetails.name} className="img-fluid" style={{ maxWidth: '100%' }} />
                </Slider>
            </div>

            <div className="col-md-6 adoptdetail-col rounded-3 mt-3">
                <div className="adetail">
                <p>Type: {dogDetails.type}</p>
                <p>Age: {dogDetails.age}</p>
                <p>Sex: {dogDetails.sex}</p>
                <p>Info: {dogDetails.info}</p>
                <Link to="/Adoptform" className="btn bg-light w-50">
                    Adopt
                </Link>
                </div>
            </div>
            </div>
        </div>
      </div><br/>
      <div className='text-center'>
        <Link to="/Adopt" style={{ textDecoration: 'none' }}>
            <img src="https://cdn-icons-png.flaticon.com/128/8744/8744133.png" style={{ width: '70px', height: '70px' }} />
            <p className='text-center my-2 text-dark'>Back to List of Dogs</p>
            </Link>
                </div>
      <br/><br/><br/><br/><br/>
      <Footer/>
    </div>
  );
};

export default AdoptDetail;

