import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom'; 
import Search from '../components/Search';
import Nav from '../components/Nav';

let DogList = () => {
    let [dogList, setDogList] = useState([]);

    let [filteredDogList, setFilteredDogList] = useState([]);

    // let searchBar = (searchTerm) => {
    //     let filteredResults = dogList.filter(
    //         (dog) =>
    //             dog.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    //             dog.type.toLowerCase().includes(searchTerm.toLowerCase()) ||
    //             dog.age.toLowerCase().includes(searchTerm.toLowerCase()) ||
    //             dog.sex.toLowerCase().includes(searchTerm.toLowerCase())
    //     );
    //     setFilteredDogList(filteredResults);
    // };

    // let searchBar = (filters) => {
    //     let filteredResults = dogList.filter((dog) => {
    //         const nameMatch =
    //             (dog.name && dog.name.toLowerCase().includes(filters.name.toLowerCase())) ||
    //             (dog.type && dog.type.toLowerCase().includes(filters.name.toLowerCase()));
    
    //         const ageMatch = !filters.age || (dog.age && dog.age.toLowerCase() === filters.age.toLowerCase());
    //         const sexMatch = !filters.sex || (dog.sex && dog.sex.toLowerCase() === filters.sex.toLowerCase());
    
    //         return nameMatch && ageMatch && sexMatch;
    //     });
    
    //     setFilteredDogList(filteredResults);
    // };
    
    let searchBar = (filters) => {
        let filteredResults = dogList.filter((dog) => {
            const nameMatch =
                (dog.name && dog.name.toLowerCase().includes(filters.name.toLowerCase())) ||
                (dog.type && dog.type.toLowerCase().includes(filters.name.toLowerCase()));
    
            const ageMatch = !filters.age || (dog.age && dog.age.toLowerCase() === filters.age.toLowerCase());
            const sexMatch = !filters.sex || (dog.sex && dog.sex.toLowerCase() === filters.sex.toLowerCase());
    
            return nameMatch && ageMatch && sexMatch;
        });
    
        setFilteredDogList(filteredResults);
    };
    
    useEffect(() => {
        fetch('http://localhost/capstone2/dog.php') 
            .then(response => response.json())
            .then(data => setDogList(data))
            .catch(error => console.error('Error fetching dog list:', error));
    }, []);

    return (
        <div>

            <Search onSearch={searchBar} />

            <div className="card-container dog-container d-flex flex-wrap justify-content-center ">
            {(filteredDogList.length > 0 ? filteredDogList : dogList).map(dog => (
                    <div key={dog.doglist_id} className="card m-3 doglist-card outline-0" style={{ height: '350px', width: '16rem' }}>
                        <img className="card-img-top p-3" src={dog.fileimg} alt={dog.name} style={{ height: '200px', width: '16rem' }} />

                        <div className="card-body">
                            <h3 className="card-title">{dog.name}</h3>
                            <p className="card-text">{dog.type}</p>
                            <p className="hidden">{dog.age}</p>
                            <p className="hidden">{dog.sex}</p>
                            {/* <Link to={`/AdoptDetail/${dog.doglist_id}`} className="btn search-btn2">
                                +
                            </Link> */}
                            <Link to={`/AdoptDetail/${dog.doglist_id}`} >
                                <img src="https://cdn-icons-png.flaticon.com/128/2064/2064847.png" style={{height: '30px', width: '30px'}}></img>
                            </Link>
                            
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default DogList;



